
import { Component } from "react";
import axios from "axios"
import {NavLink} from 'react-router-dom'

class Register extends Component{
    user={
        first_name:"",
        last_name:"",
        email:"",
        password:"",
    }
    state={errorMessage:"", inputsRequired:""}
    getFormData=(e)=>{
     this.user[e.target.name]=e.target.value
     console.log(this.user);
    }
    sendData=async (e)=>{
        e.preventDefault()
        
    let {data}=await axios.post("https://route-egypt-api.herokuapp.com/signup",this.user)
    if(data.message==="success"){
        this.props.history.replace("/Login");
    }else if(this.user.email==""||this.user.first_name==""||this.user.password==""||this.user.last_name==""){
       this.setState({
        inputsRequired: "all inputs are required"
       })
    } else if(data.message=="citizen validation failed: email: email already registered") {
        this.setState({
            inputsRequired: "",
            errorMessage: "email already registered"
        })
    }

    console.log(data.message);
    }
    render(){
        return(
           <>
           <div className="w-75 mx-auto my-5">
               <form className="mb-5" onSubmit={this.sendData}>
                   <input onChange={this.getFormData} className="form-control mt-3" type="text" placeholder="First name" name="first_name" id="" />
                   <input onChange={this.getFormData} className="form-control mt-3" type="text" placeholder="Last name" name="last_name" id="" />
                   <input onChange={this.getFormData} className="form-control mt-3" type="email" placeholder="email" name="email" id="" />
                   <input onChange={this.getFormData} className="form-control mt-3" type="password" placeholder="password" name="password" id="" />
                   <div className="text-danger text-center mt-3">{this.state.errorMessage}</div>
                   <div className="text-danger text-center mt-3">{this.state.inputsRequired}</div>
                   <button className="btn btn-info mt-3 float-right">Register</button>
               </form>
               <p className="d-inline mr-3"> have an account</p>
               <NavLink to="/login"><button className="btn btn-primary">login</button></NavLink>
           </div>
           </>
        )
    }
}
export default Register