import { Component } from "react";
import axios from "axios"
// import Styles from './Home.module.css'
class Home extends Component {
    
    state={Movies:[], tv:[]}
    getTrending=async(mediaType)=>{
        let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/day?api_key=52bbcddeda849047525b51d6f8a12361`);
        console.log(data.results);
        this.setState({
            [mediaType]:data.results
        })
    }
    // state={tv:[]}

//     change=()=>{
//    this.setState({
//        userName:'Ali'
//    })
//     }
//     componentDidUpdate(){
//         console.log('update');
//     }
    componentDidMount=()=>{
        this.getTrending("Movies");
        this.getTrending("tv");
     
    }
//     componentWillUnmount(){
//         console.log('bye');
//     }
    render() {
       
        return (
            <>

                 
                <div className='container'>
                    <div className='row'>
                        <div className="col-md-4">
                            <div className="brdr w-25">

                            </div>
                            <h1 className='mb-3'>Trending <br/> movies <br /> to watch now</h1>
                            <p className='secondFontColor'>Most Watched Movies By Day</p>
                            <div className="brdr w-25">
                                
                                </div>
                        </div>
                        {this.state.Movies.slice(0,10).map((value, index) => {
                            return (
                                <div key={index} className='col-md-2 my-3'>
                                  <div className="item mx-2">
                                    <img className='w-100' src={"https://image.tmdb.org/t/p/original"+value.poster_path} alt="" />
                                    <h3>{value.title}</h3>
                                    <h3>{value.name}</h3>
                        
                                    <span className='vote'>{value.vote_average}</span>
                                    </div>
                                </div>


                            )
                        })}
                    </div>
                </div>
                <div className='container mt-5'>
                    <div className='row'>
                        <div className="col-md-4">
                            <div className="brdr w-25">

                            </div>
                            <h1 className='mb-3'>Trending <br/> tv <br /> to watch now</h1>
                            <p className='secondFontColor'>Most Watched tv By Day</p>
                            <div className="brdr w-25">
                                
                                </div>
                        </div>
                        {this.state.tv.slice(0,10).map((value,index) => {
                            return (
                                <div key={index} className='col-md-2 my-3'>
                                  <div className="item mx-2">
                                    <img className='w-100' src={"https://image.tmdb.org/t/p/original"+value.poster_path} alt="" />
                                    <h3>{value.name} </h3>
                                    <span className='vote'>{value.vote_average}</span>
                                    </div>
                                </div>


                            )
                        })}
                    </div>
                </div>

            </>
        )
    }
}
export default Home