import { Component } from "react";
import axios from "axios"
class Movies extends Component {
    state = { Movies: [] }
    getTrending = async () => {
        let { data } = await axios.get(`https://api.themoviedb.org/3/trending/movie/day?api_key=52bbcddeda849047525b51d6f8a12361`);
        console.log(data.results);
        this.setState({
            Movies: data.results
        })
    }
    componentDidMount = () => {
        this.getTrending();
    }
    render() {
        return (
            <>
                <div className='container'>
                    <div className='row'>
                        <div className="col-md-4">
                            <div className="brdr w-25">

                            </div>
                            <h1 className='mb-3'>Trending <br /> movies <br /> to watch now</h1>
                            <p className='secondFontColor'>Most Watched Movies By Day</p>
                            <div className="brdr w-25">

                            </div>
                        </div>
                        {this.state.Movies.map((value, index) => {
                            return (
                                <div key={index} className='col-md-2 my-3'>
                                    <div className="item mx-2">
                                        <img className='w-100' src={"https://image.tmdb.org/t/p/original" + value.poster_path} alt="" />
                                        <h3>{value.title}</h3>
                                        <h3>{value.name}</h3>

                                        <span className='vote'>{value.vote_average}</span>
                                    </div>
                                </div>


                            )
                        })}
                    </div>
                </div>
            </>
        )
    }
}
export default Movies