import { Component } from "react";
import axios from "axios"
import {NavLink} from 'react-router-dom'
class Login extends Component{
    user={
     
        email:"",
        password:"",
    }
    state={errorMessage:"", inputsRequired:""}
    getFormData=(e)=>{
     this.user[e.target.name]=e.target.value
     console.log(this.user);
    }
    sendData=async (e)=>{
        e.preventDefault()

    let {data}=await axios.post("https://route-egypt-api.herokuapp.com/signin",this.user)
    if(this.user.email===""&&this.user.password===""){
     this.setState({
         inputsRequired: "email & password are required",
         errorMessage: ""
     })
    }
    else if(data.message==="success"){
        this.props.history.replace("/home");
        localStorage.setItem("token",data.token)
    }else{
        this.setState({
            errorMessage: data.message,
            inputsRequired: ""
        })
    }
    console.log(data)
    }
    render(){
        return(
          
                 <>
           <div className="w-75 mx-auto my-5">
               <form className="mb-5" onSubmit={this.sendData}>
                  
                   <input onChange={this.getFormData} className="form-control mt-3" type="email" placeholder="email" name="email" id="" />
                   <input onChange={this.getFormData} className="form-control mt-3" type="password" placeholder="password" name="password" id="" />
                   <div className="text-danger text-center mt-3">{this.state.errorMessage}</div>
                   <div className="text-danger text-center mt-3">{this.state.inputsRequired}</div>
                   <button className="btn btn-info mt-3 float-right">Login</button>
                  
               </form>
               <p className="d-inline mr-3">don't have an account</p>
               <NavLink to="/register"><button className="btn btn-primary">register</button></NavLink>
           </div>
           </>
            
        )
    }
}
export default Login